body {
  color: var(--first-text-color);
  font-family: DM Sans, sans-serif;
  font-size: 16px;
}

.link {
  text-decoration: none;
}

.list {
  list-style: none;
}

.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

p, h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

ul, ol {
  margin: 0;
  padding-left: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.container {
  width: 100%;
  outline: 1px solid #e02291;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 320px) {
  .container {
    max-width: 320px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    max-width: 1280px;
    padding-left: 16px;
    padding-right: 16px;
  }
}

.title {
  text-align: center;
  letter-spacing: .04em;
  text-transform: uppercase;
  font-family: Titan One;
  font-size: 34px;
  font-weight: 400;
  line-height: 1.15;
}

@media screen and (min-width: 768px) {
  .title {
    font-size: 48px;
  }
}

@media screen and (min-width: 1280px) {
  .title {
    letter-spacing: .06em;
    font-size: 58px;
    line-height: 1.14;
  }
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

:root {
  --first-text-color: #fff;
  --second-text-color: #d41443;
  --third-text-color: #ffa5ba;
  --fourth-text-color: #000;
  --fifth-text-color: #907e82;
  --top-body-bg-color: #e5e5e5;
  --bottom-body-bg-color: #fff5f6;
  --first-bg-color: #ffb8ca;
  --second-bg-color: #f0d1a5;
  --third-bg-color: #c2e297;
  --modal-bg-color: #e17992;
}

